	module.exports = {"data":{"markdownRemark":{"html":"<h2>CSS3 Colors</h2>\n<p>Colors in CSS are used to colorize elements in our web-pages. There are many ways to assign colors to elements. You can use the actual names for the colors, their RGB values or the Hexadecimal values.</p>\n<h3>Color Names</h3>\n<p>Currently there are 140 color names supported in HTML, which can be assigned in CSS rules by just typing their name. For example:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">color</span><span class=\"token punctuation\">:</span> green<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>This rule changes all the font color for all &#x3C;p> elements to green.<br>\nFor the full 140 color list see here: <a href=\"https://www.w3schools.com/colors/colors_names.asp\">https://www.w3schools.com/colors/colors_names.asp</a></p>\n<h3>RGB values</h3>\n<p>RGB stands for “Red”, “Green” and “Blue” and we can also assign colors by typing their RGB values in our rules. An RGB value would look like this: rgb(255,0,0), where each number defines how much of each color will be in the final mix.<br> </p>\n<p>The values range from 0 to 255 and in our example we see that only the first value is 255 while the other two are set to 0. That means that there is only red in our value and the respective element will be colored red. An RGB value of (0, 0, 0) would give black and a value of (255, 255, 255) would give white.<br></p>\n<p>It is impossible to try to memorize each color code and for that reason there numerous tools online for picking the colors you want for your projects. For example: <a href=\"https://www.w3schools.com/colors/colors_picker.asp\">https://www.w3schools.com/colors/colors_picker.asp</a> or <a href=\"http://htmlcolorcodes.com/color-picker/\">http://htmlcolorcodes.com/color-picker/</a>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">color</span><span class=\"token punctuation\">:</span> <span class=\"token function\">rgb</span><span class=\"token punctuation\">(</span>0, 255, 0<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>This rule changes the font color of all p elements to green, just like above.</p>\n<h3>Hexadecimal Values</h3>\n<p>Hexadecimal values are yet another way to define colors in CSS and they work quite similarly to RGB values.<br></p>\n<p>A random hex code would like this: <code>#29432b</code>, where the first two characters after the hash stand for the amount of RED in the mix, the second two stand for the amount of Green and the last two stand for the amount of Blue.<br> </p>\n<p>The values of <code>#000000</code> and <code>#ffffff</code> stand for black and white respectively.<br>\nYou can find the specific Hexadecimal colors you require by using the same tools mentioned for the RGB values.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-css\"><code><span class=\"token selector\">p</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token property\">color</span><span class=\"token punctuation\">:</span> #00fe00<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>This rule again changes the font color of all p elements to green.</p>\n<h3>Why use RGB or HEX values?</h3>\n<p>Color names take only 140 values, while RGB and HEX values have 16,777,216 possible combinations. So if you want your projects to look exactly how you have envisioned them, you should use the two latter options and keep the color names values for mock-ups and testing purposes.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<p><a href=\"https://www.w3schools.com/colors/default.asp\">https://www.w3schools.com/colors/default.asp</a></p>","fields":{"slug":"/css/css3-colors/"},"frontmatter":{"title":"CSS3 Colors"}}},"pathContext":{"slug":"/css/css3-colors/"}}

